.popup {
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    display: flex;
    visibility: hidden;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    opacity: 0;
}